.Report{width:100%;height:100%;position:relative;border-radius:10px;overflow:hidden;color:#000}.Report::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background-image:linear-gradient(rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.9) 70%),url("../../assets/images/배경이미지.jpg");background-size:contain;background-position:center;background-repeat:no-repeat;z-index:1;border-radius:inherit}.Report .title{position:relative;z-index:10;color:#000;font-weight:bold;font-family:"Nanum Brush Script",cursive}.Report .textContent{position:absolute;left:50%;bottom:20px;transform:translateX(-50%);width:100%;z-index:10;color:#000;font-weight:bold;font-family:"Nanum Brush Script",cursive;font-size:1.5em;line-height:1.5em;padding:20px;text-align:center}.Report .point{font-weight:bolder;background-color:#ef9;line-height:2em;padding:10px 15px;border-radius:10px;color:#000}.Report .human{font-weight:bolder;background-color:#494;line-height:1.8em;padding:5px 10px;border-radius:10px;color:#fff}